import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
let store = new Vuex.Store({
  // 1. state
  state: {
    song: {},
    ppp: '',
    songName: {},
    hh: false,
    lyric: null
  },
  mutations: {
    // statestate
    // name
    songPuda (state, data) {
      state.song = data// statecity
      state.songName = data
    }
  },
  // // 2. getters
  getters: {
    // statestate
    songPuda (state, data) {
      return state.city
    }
  },
  // 3. actions
  // api
  actions: {
    // 
    // {commit, state}
    // statestate
    // commitmutations
    // name
    setCityName ({commit, state}, name) {
      // 
      // mutaions
      commit('setCity', name)
    }
  }
})

export default store



